import { useEffect, useState } from "react";
import { Card, Row, Col, Select, Flex, Table } from "antd";
import { formatMsrp, minNumber } from "./utils";
function SelectRender({ value, ship, filterValue, onChange = () => {} }) {
  const findItem = ship.find((_t) => _t.id === filterValue) ?? {};
  const options = ship
    .filter((_t) => _t.msrp > (findItem?.msrp ?? 0))
    ?.map((_t) => ({ label: _t.name, value: _t.id, ..._t }));

  return (
    <Select
      showSearch
      value={value}
      optionFilterProp="label"
      style={{ width: "100%" }}
      options={options}
      dropdownStyle={{ minHeight: "500px", padding: "20px" }}
      listHeight="460px"
      onChange={onChange}
      optionRender={(option) => {
        return (
          <Flex justify="space-between">
            <div>
              <img src={option.data.medias?.productThumbMediumAndSmall} />
            </div>
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "space-between",
                alignItems: "end",
              }}
            >
              <div span={24}>{option.data.name}</div>
              <div span={24}>{formatMsrp(option.data.msrp)}</div>
            </div>
          </Flex>
        );
      }}
    ></Select>
  );
}
function CCUHappy({ ship = [] }) {
  const [fromValue, setFromValue] = useState();
  const [toValue, setToValue] = useState();
  const [ccu, setCCU] = useState([]);
  useEffect(() => {
    if (!!fromValue && !!toValue) {
      const _ccu = [];
      const fromValueIndex = ship.findIndex((_t) => _t.id === fromValue) ?? -1;
      const toValueIndex = ship.findIndex((_t) => _t.id === toValue) ?? -1;
      const level = {};
      if (ship[fromValueIndex].msrp < ship[toValueIndex].msrp) {
        _ccu.push({ item: ship[fromValueIndex], c: ship[fromValueIndex].msrp });
        const splicArr = ship.slice(fromValueIndex, toValueIndex + 1) ?? [];
        let lastItem = ship[fromValueIndex];
        splicArr.map((_t) => {
          const minprice = minNumber(_t?.skus)?.[0] ?? { price: _t?.msrp };
          level[minprice?.price] = _t;
        });
        Object.keys(level).map((_k) => {
          if (
            Number(_k - lastItem?.msrp) >= 500 &&
            level[_k]?.msrp < ship[toValueIndex].msrp
          ) {
            _ccu.push({
              item: level[_k],
              c: Number(_k - lastItem?.msrp),
            });
            lastItem = level[_k];
          }
        });
        const minprice = minNumber(ship[toValueIndex]?.skus)?.[0] ?? {
          price: ship[toValueIndex]?.msrp,
        };
        _ccu.push({
          item: ship[toValueIndex],
          c: Number(minprice?.price - lastItem.msrp),
        });
        setCCU(_ccu);
      }
    }
  }, [fromValue, toValue]);
  let numsum = 0;
  return (
    <Card title="CCU 叠叠乐" style={{ marginTop: "40px" }}>
      <Row gutter={16}>
        <Col span={12}>
          <SelectRender
            ship={ship}
            value={fromValue}
            onChange={(val) => {
              if (val > toValue) {
                setToValue(null);
                setCCU([]);
              }
              setFromValue(val);
            }}
          ></SelectRender>
        </Col>
        <Col span={12}>
          <SelectRender
            ship={ship}
            value={toValue}
            filterValue={fromValue}
            onChange={(val) => {
              setToValue(val);
            }}
          ></SelectRender>
        </Col>
      </Row>
      <Card style={{ marginTop: "20px" }}>
        <Table
          title={() => "路线图"}
          pagination={false}
          scroll={{
            y: 55 * 20,
          }}
          columns={[
            {
              title: "船",
              dataIndex: "ship",
              key: "ship",
            },
            {
              title: "原价",
              dataIndex: "msrp",
              key: "msrp",
              render: (_, recode) => {
                return _?("$" + _):"";
              },
            },
            {
              title: "优惠价",
              dataIndex: "price",
              key: "price",
              render: (_, recode) => {
                const _diff = Number(recode.msrp) - Number(recode.price);
                if (_diff > 0) {
                  return (
                    <div>
                      ${_}&nbsp;&nbsp;&nbsp;
                      <font color="red">{"-$" + _diff}</font>
                    </div>
                  );
                } else {
                  return _?("$" + _):"";
                }
              },
            },
            {
              title: "叠叠乐价格",
              dataIndex: "diff",
              key: "diff",
              render: (_, recode) => {
                return "$" + _;
              },
            },
          ]}
          dataSource={[...ccu.map((_t) => {
            const minprice = minNumber(_t.item?.skus)?.[0] ?? {
              price: _t.item?.msrp,
            };
            numsum+=_t?.c;
            return {
              ship: _t.item.name,
              msrp: formatMsrp(_t.item.msrp),
              price: formatMsrp(minprice?.price),
              diff: formatMsrp(_t?.c),
            };
          }),{
            ship: "",
            msrp: "",
            price: "",
            diff: formatMsrp(numsum),
          }]}
        ></Table>
      </Card>
    </Card>
  );
}

export default CCUHappy;
