import { Card, Row, Col, Flex } from "antd";
import {formatMsrp} from "./utils";
function ChildrenShip({ ship = [] }) {
  return (
    <Row
      className="ship-item"
      style={{ height: "calc(100vh - 120px)", overflowY: "auto",position:"relative" }}
      gutter={16}
    >
      {ship.map((_t,_i) => {
        return (
          <Col span={24} key={_i}>
            <Card>
              <Flex justify="space-between">
                <img alt={_t.name} src={_t.medias.slideShow} />
                <div style={{display:"flex",flexDirection:"column",justifyContent:"space-between",alignItems:"end"}}>
                  <strong style={{fontSize:"24px"}}>{_t.name}</strong>
                  <strong style={{fontSize:"24px"}}>{formatMsrp(_t.msrp)}</strong>
                </div>
                
              </Flex>
            </Card>
          </Col>
        );
      })}
    </Row>
  );
}

export default ChildrenShip;
