import { useEffect, useState } from "react";
import { Card, Row, Col, Select, Flex, Table,Typography } from "antd";
import { formatMsrp, minNumber } from "./utils";

function TableCCU({ title = "", ccu = [] }) {
  return (
    <Table
      title={()=><Typography.Paragraph><h4>{title}</h4></Typography.Paragraph>}
      pagination={false}
      scroll={{
        y: 55 * 20,
      }}
      columns={[
        {
          title: "船",
          dataIndex: "ship",
          key: "ship",
          render: (_, recode) => {
            return _;
          },
        },
        {
          title: "原价",
          dataIndex: "msrp",
          key: "msrp",
          render: (_, recode) => {
            return "$"+_;
          },
        },
        {
          title: "优惠价",
          dataIndex: "price",
          key: "price",
          render: (_, recode) => {
            return "$"+_;
          },
        },
      ]}
      dataSource={ccu.map((_t) => {
        const minprice = minNumber(_t?.skus)?.[0] ?? {
          price: _t?.msrp,
        };
        return {
          ship: _t?.name ?? "",
          msrp: formatMsrp(_t?.msrp ?? 0),
          price: formatMsrp(minprice?.price),
        };
      })}
    ></Table>
  );
}
function HistoryCCU({ historyShip = [], nowShip = [] }) {
  const _historyCCU = historyShip.filter((_t) => _t.skus?.length > 1) ?? [];
  const _nowCCU = nowShip.filter((_t) => _t.skus?.length > 1)?.map(_t=>{
   const findCCU = historyShip.find(_t1=>_t1.id===_t.id)??{};
   if(findCCU.id){
    return {...findCCU,..._t,}
   }else{
    return _t;
   }
    
  }) ?? [];
  _nowCCU.sort((a,b)=>a.msrp - b.msrp)
  return (
    <Row>
      <Col span={12}>
        <TableCCU ccu={_historyCCU} title="历史ccu" />
      </Col>
      <Col span={12}>
        <TableCCU ccu={_nowCCU} title="当前ccu" />
      </Col>
    </Row>
  );
}

export default HistoryCCU;
